.spinner {
	display: inline-block;
	vertical-align: middle;
	transform: scale(1.5);

	width: 40px;
	height: 40px;

	position: relative;
	margin: 100px auto;
}


.double-bounce1, .double-bounce2 {
	width: 100%;
	height: 100%;
	border-radius: 50%;
	background-color: #36a8e0;
	opacity: 0.6;
	position: absolute;
	top: 0;
	left: 0;


	animation: sk-bounce 2.0s infinite ease-in-out;
}

.double-bounce2 {
	animation-delay: -1.0s;
	background-color: rgba(0, 132, 255,0.7);
}

@keyframes sk-bounce {
	0%, 100% {
		transform: scale(0.0);
	} 50% {
		  transform: scale(1.0);
	  }
}

.spinner-holder {
	position: relative;
}

.spinner-lock {
	height: 100%;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	background: rgba(255,255,255,0.7);
	z-index: 10;
	text-align: center;
}
.spinner-lock:before {
	content: ' ';
	height: 100%;
	width: 0;
	display: inline-block;
	vertical-align: middle;
}

.navbar-fixed {
	z-index: 100 !important;
}

