	line-height: 20px;
	position: absolute;
	top: 50%;
	display: block;
	width: 20px;
	height: 20px;
	padding: 0;
	-webkit-transform: translate(0, -50%);
	transform: translate(0, -50%);
	cursor: pointer;
	border: none;
	outline: none;
	background: transparent;
	left: -25px;
}
.slick-next-custom {
	line-height: 20px;
	position: absolute;
	top: 50%;
