#mapModal #map {
	width: 100%;
	height: 60vh;
}

.doc-list .doc {
	width: 150px;
	height: 164px;
	display: inline-block;
	text-align: center;
	position: relative;
	transition: opacity 0.2s linear;
	cursor: pointer;
}
.doc-list .doc .icon {
	width: 128px;
	height: 128px;
	display: inline-block;
}
.doc-list .doc.pdf .icon{
	background: url("/images/pdf-icon.png");
}
.doc-list .doc.image .icon{
	background: url("/images/image-icon.png");
}
.doc-list .doc.docx .icon{
	background: url("/images/docx-icon.png");
}
.doc-list .doc.add .icon{
	background: url("/images/add-icon.png");
}

.doc-list .doc.add {
	cursor: pointer;
}

.doc-list .doc .delete {
	position: absolute;
	top: 0;
	right: 0;
	opacity: 0;
	transition: opacity 0.2s linear;
	font-size: 30px;
	cursor: pointer;
	color: white;
	background: rgba(0,0,0,0.5);
	width: 24px;
	height: 24px;
	line-height: 20px;
}

.doc-list .doc .restore {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	background: lightgreen;
	color: #555;
	font-size: 14px;
	padding: 2px 10px;
	border: 1px solid rgba(0,0,0,0.3);
	cursor: pointer;
	pointer-events: none;
	opacity: 0;
	transition: all 0.2s linear;
}

.doc-list .doc:not(.deleting):hover .delete {
	opacity: 1;
}

.doc-list .doc.deleting .icon, .doc-list .doc.deleting .name {
	opacity: 0.5;
	pointer-events: none;
}

.doc-list .doc.deleting .restore {
	opacity: 1;
	pointer-events: all;
}

.dublicat-box:after {
	content: '';
	display: block;
	clear: both;
}
.icon-compact-info {
	width: 15px;
